.contacts{
    width: 100%;
    height: 40vh;
    background-color: brown;
}
.wrap{
    max-width: 700px;
    text-align: center;
margin: 0px 350px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.light{
    color: yellow;
}
.container-top {margin-top: 20px;
 }
.text-center{
    color: #fff;
    font-size: 15px;
}
.contacts i{
    font-size: 40px;
    margin-bottom: 10px;
    color: #fff;
}
.contacts h3{
    margin-bottom: 20px;
}
.contacts p{
    color: #fff;
    font-weight: 400;
    line-height: 1.3rem;
}
.email{
    margin-top: 30px;
}
.email a {
    padding: 10px;
    font-size: 15px;
    border-radius: 10px;
    border: 1px solid #fff;
    color: #fff;
    background-color: deeppink;

}
@media(max-width:2560px){
    .contacts{
        width: 100%;
        height: 10vh;
        background-color: brown;
    }
    .wrap{
        max-width: 100%;
     margin: 0px;
    }
}
@media(max-width:1440px){

    .contacts{
        width: 100%;
        height: 29vh;
        background-color: brown;
    }
    .wrap{
        max-width: 90%;
        margin: 0px 350px;
    }
}
@media(max-width:1024px){
    .wrap{
        max-width: 100%;
        margin: 0px 70px;
    }
}
@media(max-width:768px){
    .contacts{
        width: 100%;
        height: 45vh;
        background-color: brown;
    }
}
@media(max-width:425px){
    .contacts{
        width: 100%;
        height: 60vh;
        background-color: brown;
    }
    .wrap{
        max-width: 100%;
        margin: 0px 20px;
        text-align: left;
    }
}
@media(max-width:320px){
    .wrap{
        max-width: 100%;
        margin: 0px 20px;
        text-align: left;
    }
}