:root{
    --mainLightBlue:#993203;
}
.grid2 {width: 100%;
    padding: 20px 30px 60px;
    /* text-align: center; */
    /* margin-left: 90px; */
    background: #FFF7F2;
}
.grid2 span{
    /* width: 100px; */
    max-width: 200px;
margin-top: 40px;
text-align: center;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(68, 88, 144, 0.1) 0px 10px 29px 0px;
    padding: 30px 0px 20px; 
    /* margin-top: 10px; */
    border-radius: 20px;
    border-bottom: 4px solid var(--mainLightBlue);
} 


@media(max-width:768px) {

.grid2 {
    width: 100%;
    padding: 0;
    /* height: 150px; */
    background: #FFF7F2;
}    

.grid2 span{
    width: 120px;
    margin-top: 40px;
    text-align: center;
        background-color: rgb(255, 255, 255);
        box-shadow: rgba(68, 88, 144, 0.1) 0px 10px 29px 0px;
        /* padding: 10px ;  */
        margin-left: 10px;
        margin-bottom: 20px;
        border-radius: 20px;
        border-bottom: 4px solid var(--mainLightBlue);

}
}
@media(max-width:425px){

    .grid2 {
        width: 100%;
        padding: 0;
        /* height: 150px; */
        background: #FFF7F2;
    }  

    .grid2 span{
        width: 100%;
        margin-top: 40px;
        text-align: center;
            background: transparent;
            box-shadow: none;
            /* padding: 10px ;  */
            margin-left: 0px;
            margin-right: 20px;
            margin-bottom: 20px;
            border-radius: 20px;
            border-bottom: none;
    
    }
}
