.shift{width: 100%;
    padding: 2rem;
    /* position: relative; */
    display: flex;
    /* flex-direction: column; */
    gap: 2rem;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
}
.motionsd{
    background-color: transparent;
    text-align: center;
}
.motionsd h3{
    font-size: 30px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 500;
}
.motionsd p{
    letter-spacing: 1px;
}
.producted{
    width: 100%;
    text-align: center;
    display: block;
}
 .menu{
    display: grid;
    
    grid-template-columns: repeat(4,1fr);
   list-style: none;
   /* display: flex; */
   /* flex-direction: column; */
   gap: 2rem;
/* margin-right: 50px; */
   grid-template: 2rem;
   font-weight: 500;
   font-size: 1.3rem;
   margin-bottom: 30px;
} 

.menu >li:hover{
    color: var(--black); 
    cursor: pointer;
}
