.producre{
    
    position: relative;
    /* box-sizing: border-box; */
    display: inline-block;
     background: rgb(121, 45, 1);
    border-radius: 10px;
    margin: 10px;

}
.producre img{
    width: 45vh;
    height: 30vh;
    object-fit: cover;
    transition: transform 2s ease 0s; 
}

.overlay{
    /* display: flex; */
    /* height: 40px; */
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity 0.45s ease-in-out;
    background: #fff;
    border-radius: 10px;
        /* transform: scaleY(0); */

    box-shadow: 5PX 7PX 25px rgba(0,0,0,0.3);

}
.content{
   

    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
background:transparent;
    transform: translate(-50%,-50%);
    color: #fff;
    font-family: Verdana, sans-serif;
    text-align: center;
}
 
.content h3{
    margin-top: 30px;
    color: #000;
    font-weight: bold
    ;font-size: 1.3rem;
    font-family: 'poppins',sans-serif;
}
.content a {
    width: 7rem;
    margin-top: 60px;
    cursor: pointer;
padding: 9px;
color: #fff;
border-radius: 15px;
background:#65361B;
/* opacity: 1.3; */
position: relative;
    margin-left: 30px;
    transition: all 0.5s;
    ;font-size: 1rem;

}
.producre:hover .overlay{
    opacity: 0.8;
    box-shadow: 5PX 7PX 25px rgba(0,0,0,0.3);
}
.producre a:hover{
    background-color: lightblue;
    color: #000;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);

       /* border: 1px solid saddlebrown */
}

.list {
   list-style-type: none;
   display: grid;
   grid-template-columns: repeat(4,1fr);
}

.producre ul li{
    /* border: 2px solid goldenrod; */
    /* padding: 4rem; */
     margin: 10px;
    border-radius: 5%;
}
