.product{
    width: 100%;
    height: 100vh;
    min-height: 100vh;
    background: #f5f5f5;
}
.container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 4rem;
}
.img{
    width: 400px;
    height: 400px;
    border-radius: 50%;
    overflow: hidden;
    border: 15PX solid #444;
    box-shadow: 5PX 7PX 25px rgba(0,0,0,0.3);
}
.img img{
    height: 100%;
    width: 100%;
    transition: 0.5s;
}
.img img:hover{
    transform: scale(1.3);
}
.h-text{
     max-width: 600px;
    display: flex;
    flex-direction: column;
    text-align: center;
}
.h-text p{
    color: #444;
    line-height: 20px;
   font-size: 15px;
   /* margin-right: 80px; */
text-align: left;
margin-top: 10px;
   /* max-width: 500px; */
   position: relative;
   letter-spacing: 1px;
} 
.h-text h4{
    color: #222;
    text-align: left;
    /* margin-right: 70px; */
    font-size: 2.5rem;
}
 
.product button:hover{
    background-color: #fff;
    color: orangered;
    font-weight: 800;
    /* font-size: 16px; */
    border: 1px solid salmon;

}
.move button{
    cursor: pointer;
    /* margin: 0 40em ; */
border-radius: 25px;
position: absolute;
    padding: 20px;
    background-color: orangered;
    opacity: 0.8;
    color: #fff;
    transition: 1s;
    right: 20px;
    margin: auto 350px;
 }
 @media(max-width:2560px) {
  
    .move button{
        cursor: pointer;
         display: block;
        /* margin-left: 95rem; */
        /* margin-bottom: 10px; */
    }
    .product{
        height: 10px;
        min-height: 45vh;
     }
}
@media(max-width:1440px) {
    .container{
        display: flex;
        justify-content: space-around;
